// Document
// 1.box-sizing=border-box
// 2.
// 3.line-height
// 4.iOS
// 5.iOs

*,
*::before,
*::after {
  box-sizing: border-box; //1
}

html{
  font-family: sans-serif; // 2
  line-height: 1.15; //3
  -webkit-text-size-adjust: 100%; // 4
  -webkit-tap-highlight-color: rgba($black, 0); // 5
}

// html5(IE10)
article, aside, figcaption, figure, footer, header, hgroup, main, nav, section {
  display: block;
}

// body
// 1.bodymargin=0
// 2.<th>inherit
// 3.

body {
  margin: 0; // 1
  font-family: $font-family-base;
  @include font-size($font-size-base);
  font-weight: $font-weight-base;
  line-height: $line-height-base;
  color: $body-color;
  text-align: left; // 2
  background-color: $body-bg; // 3
}

// 
// 

[tabindex="-1"]:focus {
  outline: 0 !important;
}

// hr
// 1. firefoxbox-sizing
// 2. IEEdge

hr {
  box-sizing: content-box; // 1
  height: 0; // 1
  overflow: visible; // 2
}

// header
// hmargin
// margin
h1, h2, h3, h4, h5, h6 {
  margin-top: 0;
  margin-bottom: $headings-margin-bottom;
}

// paragraph
// hpmargin
// emremmargin
p {
  margin-top: 0;
  margin-bottom: $paragraph-margin-bottom;
}

// Abbreviations
// 1.data-*
// 2.Chrome, Edge, IE, Opera, Safari
// 3.
// 4.firefox 39
// 5.
abbr[title],
abbr[data-original-title] { // 1
  text-decoration: underline; // 2
  text-decoration: underline dotted; // 2
  cursor: help; // 3
  border-bottom: 0; // 4
  text-decoration-skip-ink: none; // 5
}

address {
  margin-bottom: 1rem;
  font-style: normal;
  line-height: inherit;
}

ol,
ul {
  padding-left: 0; //padding-left40
}

ol,
ul,
dl {
  margin-top: 0;
  margin-bottom: 1rem;
}

ol ol,
ul ul,
ol ul,
ul ol {
  margin-bottom: 0;
}

dt {
  font-weight: $dt-font-weight;
}
  
dd {
  margin-bottom: .5rem;
  margin-left: 0; //
}
  
blockquote {
  margin: 0 0 1rem;
}

// Chrome, Edge, Safari
b,
strong {
  font-weight: $font-weight-bolder;
}

// 
small {
  @include font-size(80%);
}

// subsup
sub,
sup {
  position: relative;
  @include font-size(75%);
  line-height: 0;
  vertical-align: baseline;
}

sub { bottom: -.25em; }
sup { top: -.5em; }


// link
a {
  color: $link-color;
  text-decoration: $link-decoration;
  background-color: transparent; // IE10
  
  &:hover {
    color: $link-hover-color;
    text-decoration: $link-hover-decoration;
  }
}

// (href)
a:not([href]) {
  &,
  &:hover {
    color: inherit;
    text-decoration: none;
  }
}
  

// code
pre,
code,
kbd,
samp {
  font-family: $font-family-monospace;
  @include font-size(1em); // em
}

pre {
  margin-top: 0;
  margin-bottom: 1rem; // rem
  overflow: auto; // 
}

// figure
figure {
  margin: 0 0 1rem; // margin
}

//img, content
img {
  vertical-align: middle;
  border-style: none; // IEimgborder
}

svg {
  overflow: hidden; // IEoverflow
  vertical-align: middle;
}


// table
table {
  border-collapse: collapse; //border
}

caption {
  padding-top: $table-cell-padding;
  padding-bottom: $table-cell-padding;
  color: $table-caption-color;
  text-align: left;
  caption-side: bottom;
}

th {
  // body
  text-align: inherit;
}

// form
label {
  display: inline-block;
  margin-bottom: $label-margin-bottom;
}

button {
  border-radius: 0; // MACOSChromemargin-radius
}

// buttonbutton
button:focus {
  outline: 1px dotted;
  outline: 5px auto -webkit-focus-ring-color;
}

input,
button,
select,
optgroup,
textarea {
  margin: 0; // firefoxsafarimargin
  font-family: inherit;
  @include font-size(inherit);
  line-height: inherit;
}

button,
input {
  overflow: visible; // Edgeoverflow
}

button,
select {
  text-transform: none; // firefox
}

select {
  word-wrap: normal; //safariword-wrap
}


// 1. Andoroid 4
// 2. iOSsafari
button,
[type="button"], // 1
[type="reset"],
[type="submit"] {
  -webkit-appearance: button; // 2
}

// button
button,
[type="button"],
[type="reset"],
[type="submit"] {
  &:not(:disabled) {
    cursor: pointer;
  }
}

// firefox
button::-moz-focus-inner,
[type="button"]::-moz-focus-inner,
[type="reset"]::-moz-focus-inner,
[type="submit"]::-moz-focus-inner {
  padding: 0;
  border-style: none;
}

input[type="radio"],
input[type="checkbox"] {
  box-sizing: border-box; // 1. IEbox-sizing
  padding: 0; // 2. IE
}

input[type="date"],
input[type="time"],
input[type="datetime-local"],
input[type="month"] {
  //Mobile Safari
  -webkit-appearance: listbox;
}

textarea {
  overflow: auto; // IE
  resize: vertical;
}

fieldset {
  // fieldset
  min-width: 0;
  // 
  padding: 0;
  margin: 0;
  border: 0;
}

// 1. IEEdge
// 2. IEfieldset
legend {
  display: block;
  width: 100%;
  max-width: 100%; // 1
  padding: 0;
  margin-bottom: .5rem;
  @include font-size(1.5rem);
  line-height: inherit;
  color: inherit; // 2
  white-space: normal; // 1
}

progress {
  vertical-align: baseline; // Chrome, firefox, opera
}

// 
[type="number"]::-webkit-inner-spin-button,
[type="number"]::-webkit-outer-spin-button {
  height: auto;
}

[type="search"] {
  // iOS
  outline-offset: -2px; // safari
  -webkit-appearance: none;
}

// chrome, safari macOS
[type="search"]::-webkit-search-decoration {
  -webkit-appearance: none;
}

// 1. 
// 2. safari
::-webkit-file-upload-button {
  font: inherit; // 2
  -webkit-appearance: button; // 1
}

output {
  display: inline-block;
}

summary {
  display: list-item;
  cursor: pointer;
}

template {
  display: none; // IE
}

[hidden] {
  display: none !important;
}
